.nav {
  @include color-transition(background-color);

  padding-right: 14px;
  padding-top: 14px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &--night-mode {
    @include color-transition(background-color);
    background-color: $nightMode;
  }

  &__links-wrapper {

  }

  &__link {
    @include color-transition(color);

    text-decoration: none;
    color: $nightMode;
    font-family: "Manrope-Light";

    &--night-mode {
      @include color-transition(color);
      color: $onMode;
    }
  }

  &__night-mode-button {
    background: transparent;
    border: none;

    &:focus {
      outline: none;
    }

    &--night-mode {
      color: $onMode;
    }
  }

  &__sun {
    fill: $onMode;
  }
}