.home {
  &__content {
    height: 100%;
    display: flex;
    align-items: center;

    &--mobile {
      // padding: 12px
    }
  }
  &__wrapper {
    @include color-transition(background-color);

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-sizing:border-box;
     -moz-box-sizing:border-box;
     -webkit-box-sizing:border-box;
     -ms-box-sizing:border-box;

     &--tall {
       height: 100%;
     }

     &--night-mode {
      @include color-transition(background-color);
       background-color: $nightMode;
     }
  }

  &__title {

  }

  &__interests-list {
    list-style: none;
    margin-top: 24px;
  }

  &__interests-link {
    @include color-transition(color);
    cursor: pointer;
    text-decoration: none;
    border-bottom: solid 5px;
    color: $darkPrimary;

    &--night-mode {
      @include color-transition(color);
      color: $onMode;
    }

    &--linkedin {
      border-bottom-color: $linkedin;
    }

    &--strava {
      border-bottom-color: $strava;
    }
  }

  &__interests {
    @include color-transition(color);
    max-width: 800px;
    font-size: 2em;
    line-height: 1.7em;

    &--mobile {
      padding: 10px 24px;
    }

    &--sf {
      @include color-transition(border-bottom);
      text-decoration: none;
      border-bottom: solid $onModeRed 5px;
    }

    &--sf-night-mode {
      @include color-transition(border-bottom);
      text-decoration: none;
      border-bottom: solid $darkModeRed 5px;
    }

    &--underline {
      @include color-transition(border-bottom);
      text-decoration: none;
      border-bottom: solid $primary 5px;
    }

    &--underline-night-mode {
      // @include color-transition(border-bottom);
      color: $onMode;
      border-bottom: solid $primaryPurple 5px;
    }

    &--night-mode {
      @include color-transition(color);
      color: $onMode;
    }
  }

  &__social-list {
    list-style: none;
    display: flex;
    margin-top: 24px;
  }

  &__social-list-item {
    @include color-transition(fill);

    fill: $darkPrimary;
    margin-right: 24px;

    &--night-mode {
      @include color-transition(fill);
      fill: $onMode;
    }

    &--linkedin {
      @include color-transition(fill);
      fill: $linkedin;
    }

    &--strava {
      fill: $strava;
    }
  }
}