@import "mixins";
@import "variables";
@import "home";
@import "nav";

*, html {
  margin: 0;
  padding: 0;
  font-family: "Manrope-Bold";
}

html, body {
  height: 100%;
}

body {
  background-color: $onMode;
}

.main-wrapper {
  @include color-transition(background-color);
  overflow: scroll;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: $onMode;

  &--mobile {
    padding-bottom: 12px;
  }

  &--night-mode {
    @include color-transition(background-color);
    background-color: $nightMode;
  }
}